@font-face {
  font-family: myFirstFont;
  src: url(../../components/assests/GT-Walsheim-Pro-Light.ttf);
}

.overlay {
  width: 100vw;
	height: 100vh;
	top: 0;
	left: 0;
}

.modal{
  font-family: myFirstFont;
	display: flex;
	align-items: center;
	justify-content: center;
  cursor: default;
  width: 100%;
  height: 100%;
  text-align: initial;
}

.overlay{
  position: absolute;
	z-index: 10;
	background: rgba(55, 58, 71, 0.9);
	opacity: 0;
	transition: opacity 0.3s;
}

.content{
  width: min-content;
  min-width: 250px;
  padding: 1.5rem;
  border-radius: 16px;
  background: #0f091f;
  box-shadow: -1rem 0 2rem #000;
  transition: .2s;
}

.header p{
  font-size: 17px;
  color: #e1e7e7;
  letter-spacing: 0.5px;
  margin: 10px 0 0 0;
  font-weight: 500;
  width: fit-content;
}
.header h2{
  font-size: 27px;
  cursor: pointer;
  color: #e1e7e7;
  letter-spacing: 1.5px;
  margin : 0;
  font-weight: 600;
  width: fit-content;
  display: inline-block;
}

.header h2:hover{
  background: linear-gradient(to right,#ff8a00,#e52e71);
  text-shadow: none;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  cursor: default;
}

.header{
  margin: 24px 24px 8px 24px;
  display: inline-block;
}

.desc{
  margin: 24px;
  font-weight: 500;
  margin-top: 0;
  display: inline-block;
  color: #e1e7e7;
  width: 35vw;
}

.para{
  font-family: myFirstFont;
  font-size: 17px;
  color: #e1e7e7;
  letter-spacing: 0.5px;
  font-weight: 500;
  text-align: justify;
  display: inline-block;
}

.imageHolder{
  display: inline-block;
  text-align: center;
  width: 30vw;
}

.imageHolder img{
  max-width: 80%;
  border: 2px solid #e1e7e7;
  padding: 2px;
}

.contentBox{
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.list{
  margin: 10px 0;
  font-family: myFirstFont;
  letter-spacing: 0.2px;
  font-size: 17px;
}

.cross{
  display: inline-block;
  float: right;
  padding-top: 1.6em;
  padding-right: 2em;
}

.cross img{
  width: 20px;
  height: 20px;
  transition: 0.2s;
}

.cross img:hover{
  width: 23px;
  height: 23px;
}

.cross button{
  background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}